* {
  margin: 0;
  padding: 0;
}

body {
  color: #000;
  background-color: #fff;
  flex-direction: column;
  font-family: DM Sans, sans-serif;
  display: flex;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

.customers-reviews {
  background-color: #fff;
  flex-direction: column;
  display: flex;
}

.customers-reviews__title {
  display: none;
}

.customers-reviews__picturelink {
  width: 85px;
  height: 85px;
  margin: 80px auto 29px;
}

@media screen and (min-width: 768px) {
  .customers-reviews__picturelink {
    margin: 121px auto 19px;
  }
}

@media screen and (min-width: 1280px) {
  .customers-reviews__picturelink {
    margin: 123px auto 15px;
  }
}

.customers-reviews__container {
  height: auto;
  width: 100%;
  max-width: 480px;
  min-width: 280px;
  background-color: #fafafa;
  position: relative;
}

@media screen and (min-width: 768px) {
  .customers-reviews__container {
    max-width: none;
    height: 164px;
    width: 620px;
  }
}

@media screen and (min-width: 1280px) {
  .customers-reviews__container {
    height: 138px;
    width: 926px;
  }
}

.customers-reviews__container:before {
  content: "";
  width: 54px;
  height: 40px;
  background: url("quotex1.4829de5d.png") 0 0 / 54px 40px;
  position: absolute;
  top: 28px;
  left: 61px;
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .customers-reviews__container:before {
    background: url("quotex2.f6c0310f.png") 0 0 / 54px 40px;
  }
}

@media (min-device-pixel-ratio: 3), (min-resolution: 288dpi) {
  .customers-reviews__container:before {
    background: url("quotex3.bfc3f873.png") 0 0 / 54px 40px;
  }
}

@media screen and (min-width: 768px) {
  .customers-reviews__container:before {
    top: 30px;
    left: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .customers-reviews__container:before {
    top: 32px;
    left: 50px;
  }
}

.customers-reviews__textbox {
  width: 100%;
  height: auto;
  text-align: center;
  z-index: 2;
  padding: 43px 66px 26px 57px;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 2.55;
  position: relative;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .customers-reviews__textbox {
    width: 460px;
