.customers-reviews__slider {
  width: 172px;
  height: auto;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 30px auto;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .customers-reviews__slider {
    margin: 24px auto 35px;
  }
}

@media screen and (min-width: 1280px) {
  .customers-reviews__slider {
    margin: 15px auto 35px;
  }
}

.customers-reviews__dots {
  height: 4px;
  justify-content: center;
  display: flex;
  position: absolute;
  top: 0;
}

.customers-reviews__dot--hiden {
  visibility: hidden;
}

.customers-reviews__dot {
  width: 4px;
  height: 4px;
  background-color: #c7133e;
  border-radius: 50%;
  margin: 0 5px;
}

.customers-reviews__name {
  margin: 22px auto 0;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.86;
}

.customers-reviews__name--under {
  visibility: hidden;
}

.slider {
  width: 100%;
  background-color: #fff;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.slider__slide {
  width: 100%;
  opacity: 0;
  max-width: 480px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  font-size: 50px;
  font-weight: bold;
  transition: all 1s;
  display: flex;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.slider__slide.active {
  opacity: 1;
}

.slider__nav-button {
  height: 300px;
  width: 50%;
  opacity: .8;
  cursor: pointer;
  position: absolute;
  top: 50px;
}

@media screen and (min-width: 768px) {
  .slider__nav-button {
    height: 164px;
  }
}

@media screen and (min-width: 1280px) {
  .slider__nav-button {
    height: 138px;
  }
}

.slider__nav {
  width: 90px;
  height: 12px;
  justify-content: center;
  margin: 609px auto 86px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .slider__nav {
    margin: 491px auto 119px;
  }
}

@media screen and (min-width: 1280px) {
  .slider__nav {
    margin: 461px auto 132px;
  }
}

.slider__navlink {
  height: 12px;
  width: 12px;
  opacity: .8;
  cursor: pointer;
  background-color: #d9d9d9;
  border: 1px #d9d9d9;
  border-radius: 50%;
  margin: 0 9px;
  display: flex;
  position: relative;
}

.slider__navlink.active {
  background-color: #fff;
  border: none;
  border-radius: 0%;
}

.slider__icon-house {
  width: 12px;
  height: 12px;
  fill: #d31342;
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

.active > .slider__icon-house {
  visibility: visible;
}

#nav-button--prev {
  height: 308px;
  top: 331px;
  left: 0;
  transform: translateY(-50%);
}

@media screen and (min-width: 768px) {
  #nav-button--prev {
    height: 164px;
    top: 308px;
  }
}

@media screen and (min-width: 1280px) {
