.products__item--pink {
  transition-property: color, transform;
  transition-duration: .4s;
  transition-delay: .2;
  background-color: #ffb8ca;
}

.products__item--coffee {
  transition-property: color, transform;
  transition-duration: .4s;
  transition-delay: .2;
  background-color: #f0d1a5;
}

.products__item--green {
  background-color: #c2e297;
}

.products__item--pink:hover, .products__item--pink:focus {
  transform: color;
  background-color: #fcaec0;
}

.products__item--coffee:hover, .products__item--coffee:focus {
  transform: color;
  background-color: #f4c98ef0;
}

.products__item--green:hover, .products__item--green:focus {
  transform: color;
  background-color: #b6d48e;
}

.products__item:hover .products__description, .products__item:focus .products__description {
  transform: scale(1.1);
}

.products__name {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #fff;
  transition-property: color, transform;
  transition-duration: .4s;
  transition-delay: .2;
  margin-bottom: 30px;
  font-family: Titan One;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.15;
}

@media screen and (min-width: 1280px) {
  .products__name {
    font-size: 30px;
  }
}

.products__name:after {
  content: "...";
  color: #fff;
  letter-spacing: 3px;
  margin-top: 8px;
  font-size: 25px;
  display: block;
}

.products__description {
  text-align: center;
  letter-spacing: .04em;
  color: #fff;
  max-width: 200px;
  min-width: 200px;
  transition-property: color, transform;
  transition-duration: .4s;
  transition-delay: .2;
  margin-bottom: 30px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.5;
